import { Component } from '@angular/core';
import { NavController } from 'ionic-angular';
import { BarcodeScanner , BarcodeScannerOptions} from '@ionic-native/barcode-scanner';
 import { ToastController } from 'ionic-angular';
@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})
export class HomePage {
  qrData = null;
  createdCode = null;
  scannedCode = null;
  options:BarcodeScannerOptions;
  constructor(private barcodeScanner: BarcodeScanner , public toastCtrl: ToastController) { }
 
 
 
  scanCode() {
	   this.options = {
		  
		   prompt : 'Point camera inside rectangular view to scan it'
		  
	  }
	
    this.barcodeScanner.scan(this.options).then(barcodeData => {
      this.scannedCode = 'Barcode Value : ' + barcodeData.text;
	 
	 
    
  let toast = this.toastCtrl.create({
    message: 'Barcode Value : ' + barcodeData.text,
      position: 'bottom',
	  closeButtonText:'Got it',
	showCloseButton: true,
	dismissOnPageChange : true,
	cssClass :'stmyclass'
  });
  toast.present();
	 
	 
	 
    });
  }
 
}


